<template>
    <div class="systemUser">
      <el-row>
        <el-table
          :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
          height="750px"
          style="width: 100%">
          <el-table-column
            prop="number"
            label=""
            width="50">
          </el-table-column>
          <el-table-column
            prop="account"
            width="150"
            label="">
          </el-table-column>
          <el-table-column
            prop="name"
            label=""
            width="150">
          </el-table-column>
          <el-table-column
            prop="group"
            width="200"
            label="">
          </el-table-column>
          <el-table-column
            prop="status"
            width="80"
            label="">
          </el-table-column>
          <el-table-column
            prop="create_time"
            label=""
            sortable
            width="200">
          </el-table-column>
          <el-table-column
            align="right">
            <template slot="header" slot-scope="scope">
              <div class="el-table-header">
                <el-input v-model="search"
                  placeholder=""/>
                <el-button type="primary" v-if="addSystemUser" @click.native.prevent="addInformation"></el-button>
              </div>
            </template>
            <template slot-scope="scope">
              <el-button
                @click.native.prevent="compileRow(scope.$index,tableData)"
                size="small">
                
              </el-button>
              <el-button
                @click.native.prevent="deleteRow(scope.$index,tableData)"
                type="danger"
                size="small"
              >
                
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <!---->
        <el-dialog title="" :visible.sync="dialogFormVisibleData">
          <el-form :rules="rules" :model="formData" :label-position="labelPosition" style="width: 50%;margin-left: 25%">
            <el-form-item label="" :label-width="formLabelWidth" prop="name">
              <el-input v-model="formData.name" autocomplete="off" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="account">
              <el-input v-model="formData.account" autocomplete="off" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="password">
              <el-input v-model="formData.password" autocomplete="off" placeholder="123456"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" v-if="obj" prop="group">
              <el-select v-model="formData.group" placeholder="" style="width: 100%">
                <el-option v-for="(item,key) in obj" :key="key" :label="item" :value="key"></el-option>
              </el-select>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisibleData = false"> </el-button>
            <el-button type="primary" @click.native.prevent="dataSuccess"> </el-button>
          </div>
        </el-dialog>

        <el-col :span="24">
          <div class="block" v-if="total">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="currentPage"
              layout="total, prev, pager, next"
              :total="total">
            </el-pagination>
          </div>
        </el-col>
      </el-row>
    </div>
</template>

<script>
    export default {
        data() {
            return {
              edUser:'',
              addSystemUser:false,
              obj:{},
              search:"",
              total:"",
              currentPage:"",
              dialogFormVisibleData:false,
              formLabelWidth:'80px',
              labelPosition:"center",
              tableData: [],
              formData:{
                  name:"",
                account:"",
                password:"",
                group:""
              },
              rules:{
                name:[
                  { required: true, message: '', trigger: 'blur' }
                ],
                account:[
                  { required: true, message: '', trigger: 'blur' },
                  { min: 11, max: 11, message: ' 11 ', trigger: 'blur' }
                ],
                group:[
                  { required: true, message: '', trigger: 'change' }
                ],
                password:[
                  { min: 6, max: 15, message: ' 6  15 ', trigger: 'blur' }
                ]
              },
            }
        },
        methods: {
          compileRow:function(index, rows){
            alert('',index,rows)
          },
          systemUser:function(){
            //
            var data = {
              map:"api_user_list",
              plum_session_api:localStorage.getItem('token'),
              time_stamp:Date.parse(new Date())/1000,
              page:1
            };
            data.sign_str = this.createSign(data);
            this.$http.post('',data).then((data)=>{
              if(data.ec==200){
                this.tableData =data.data.list;
                this.total = data.data.list.length;
                this.currentPage = data.current_page
              }else {
                this.$message.error(data.em);
              }
            }).catch((err)=>{
              this.$message.error('');
            });
          },
          addInformation:function () {
            this.dialogFormVisibleData = true
          },
          handleSizeChange(val) {
            console.log(` ${val} `);
          },
          handleCurrentChange(val) {
            console.log(`: ${val}`);
          },
          //
          dataSuccess:function () {
            var password = this.formData.password;
            if(password == ''){
              password = 123456
            }else {
              password = this.formData.password;
            }
            var data = {
              map:"api_user_add",
              plum_session_api:localStorage.getItem('token'),
              time_stamp:Date.parse(new Date())/1000,
              name:this.formData.name,
              account:this.formData.account,
              password:password,
              group:this.formData.group
            };
            data.sign_str = this.createSign(data);
            this.$http.post('',data).then((data)=>{
              if(data.ec==200){
                this.$message.success(data.em);
                this.dialogFormVisibleData = false;
                this.formData.name="";
                //
                this.systemUser();

              }else {
                this.$message.error(data.em);
              }
            }).catch((err)=>{
              this.$message.error('');
            });
          },
          //
          deleteRow:function(index, rows) {
            this.$confirm(', ?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
              rows.splice(index, 1);
              this.total = this.tableData.length;
              this.$message({
                type: 'success',
                message: '!'
              });
            }).catch(() => {
              this.$message({
                type: 'info',
                message: ''
              });
            });
          },
        },
        created() {
          //
          this.systemUser();
          //
          var datas = {
            map:"api_user_alive_group",
            plum_session_api:localStorage.getItem('token'),
            time_stamp:Date.parse(new Date())/1000,
          };
          datas.sign_str = this.createSign(datas);
          this.$http.post('',datas).then((data)=>{
            if(data.ec==200){
             this.obj = data.data;
            }else {
              this.$message.error(data.em);
            }
          }).catch((err)=>{
            this.$message.error('');
          });
          //
          this.buttonPermissions().then((data)=>{
            if(data == 'all'){
              this.addSystemUser =true;
            }else {
              if(data.indexOf('.addSystemUser') !== -1){
                this.addSystemUser =true
              }
            }
          })
        },
    }
</script>

<style scoped>
  .el-col {
    border-radius: 4px;
    height: 50px;
    background-color: #fff;
  }
  .el-table-header>>>.el-input{
    width: 200px;
    float: left;
    height: 40px;
  }
  .el-table-header>>>.el-button{
    float: right;
    height: 40px;
  }
  .block>>>.el-pagination{
    height: 30px;
    line-height: 30px;
    margin-top: 10px;
    text-align: center;
  }
</style>



