<template>
  <div id="pie-chart" style="width: 100%;height: 100%;"></div>
</template>

<script>
  export default {
    data() {
      return {
        charts: '',
        opinion:['','','','',''],
        opinionData:[
          {value:335, name:''},
          {value:310, name:''},
          {value:234, name:''},
          {value:135, name:''},
          {value:1548, name:''}
        ]
      }
    },
    methods: {
      drawPie(id){
        this.charts = this.$echarts.init(document.getElementById(id));
        setTimeout(window.onresize = () => {
          this.charts.resize();
        },100);
        this.charts.setOption({
          tooltip: {
            trigger: 'item',
            formatter: '{a}<br/>{b}:{c} ({d}%)'
          },
          legend: {
            orient: 'vertical',
            x: 'left',
            data:this.opinion
          },
          series: [
            {
              name:'',
              type:'pie',
              radius:['50%','70%'],
              avoidLabelOverlap: false,
              label: {
                normal: {
                  show: false,
                  position: 'center'
                },
                emphasis: {
                  show: true,
                  textStyle: {
                    fontSize: '30',
                    fontWeight: 'blod'
                  }
                }
              },
              labelLine: {
                normal: {
                  show: false
                }
              },
              data:this.opinionData
            }
          ]
        })
      }
    },
    mounted(){
      this.$nextTick(function() {
        this.drawPie('pie-chart')
      })
    }
  }
</script>




